.advantages-container {
  @extend %adaptive-container;

  padding-bottom: 94px;
  @media screen and (min-width: 768px) {
    width: 730px;
    padding-bottom: 102px;
  }
  @media screen and (min-width: 1280px) {
    width: 1080px;
    padding-bottom: 101px;
  }
}

.advantages-title {
  @extend %visually-hidden;
}

.advantages {
  padding-top: 39px;
  background-color: #fff5f6;
  @media screen and (min-width: 768px) {
    padding-top: 0;
  }
}

.advantages-list {
  @media screen and (min-width: 768px) {
    display: flex;
  }
}

.advantages-item {
  @media screen and (min-width: 768px) {
    width: 180px;
    & + & {
      margin-left: 60px;
    }
  }
  @media screen and (min-width: 1280px) {
    width: 330px;
    & + & {
      margin-left: 30px;
    }
  }
}

.advantages-item:not(:last-child) {
  padding-bottom: 38px;
}

.advantages-unit {
  display: block;
  font-family: Titan One;
  font-style: normal;
  font-size: 36px;
  line-height: 1.14;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  margin-bottom: 13px;

  color: #d41443;
  @media screen and (min-width: 768px) {
    font-size: 48px;
  }
  @media screen and (min-width: 1280px) {
    font-size: 58px;
    margin-bottom: 15px;
  }
}

.advantages-text {
  font-family: DM Sans;
  font-style: normal;
  font-weight: 700;
  font-size: 12px;
  line-height: 1.7;
  letter-spacing: 0.02em;

  color: #000000;
  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: 1.74;
  }
  @media screen and (min-width: 1280px) {
    font-size: 16px;
    line-height: 1.76;
  }
}

.advantages-unit-01::before {
  content: '';
  background-image: url(../images/advantages/advantages-icon1.png);
  width: 33px;
  height: 50px;
  display: block;
  background-repeat: no-repeat;
  background-size: cover;
  margin-bottom: 16px;
  @media screen and (min-width: 768px) {
    margin-bottom: 36px;
  }
  @media screen and (min-width: 1280px) {
    margin-bottom: 40px;
  }
}

.advantages-unit-02::before {
  content: '';
  background-image: url(../images/advantages/advantages-icon2.png);
  width: 50px;
  height: 50px;
  display: block;
  background-repeat: no-repeat;
  background-size: cover;
  margin-bottom: 16px;
  @media screen and (min-width: 768px) {
    margin-bottom: 36px;
  }
  @media screen and (min-width: 1280px) {
    margin-bottom: 40px;
  }
}

.advantages-unit-03::before {
  content: '';
  background-image: url(../images/advantages/advantages-icon3.png);
  width: 44px;
  height: 50px;
  display: block;
  background-repeat: no-repeat;
  background-size: cover;
  margin-bottom: 16px;
  @media screen and (min-width: 768px) {
    margin-bottom: 36px;
  }
  @media screen and (min-width: 1280px) {
    margin-bottom: 40px;
  }
}
