.about-container {
  @extend %adaptive-container;
}

.about {
  background-color: #fff5f6;
  padding-top: 125px;
  background-image: url(../images/about/about-bg-mob-1x.png);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: bottom;
  @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
    background-image: url(../images/about/about-bg-mob-2x.png);
  }
  @media screen and (min-width: 768px) {
    padding-top: 130px;
    background-image: none;
  }
  @media screen and (min-width: 1280px) {
    padding-top: 125px;
    background-image: url(../images/about/about-bg-pc-1x.png);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: bottom;
    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: url(../images/about/about-bg-pc-2x.png);
    }
  }
}

.about-pretitle {
  font-family: Titan One;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 1.16;
  text-align: center;
  text-transform: uppercase;

  color: #ffa5ba;

  margin-top: 0;
  margin-bottom: 15px;
  @media screen and (min-width: 768px) {
    font-size: 26px;
    line-height: 1.15;
    margin-bottom: 16px;
  }
  @media screen and (min-width: 1280px) {
    font-size: 30px;
    line-height: 1.13;
    margin-bottom: 15px;
  }
}

.about-title {
  font-family: Titan One;
  font-style: normal;
  font-weight: normal;
  font-size: 34px;
  line-height: 1.14;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;

  color: #d41443;

  margin-bottom: 26px;
  @media screen and (min-width: 768px) {
    font-size: 48px;
    margin-bottom: 50px;
  }
  @media screen and (min-width: 1280px) {
    font-size: 58px;
    line-height: 1.13;
    margin-bottom: 55px;
  }
}

.about-wrap {
  @media screen and (min-width: 768px) {
    display: flex;
  }
}

.about-left-container {
  display: flex;
  justify-content: center;
  margin-bottom: 46px;
  // @media screen and (min-width: 768px) {
  //     margin: 0;
  // }
}

.about-text-container {
  display: block;
  margin-left: auto;
  margin-right: auto;
  min-width: 220px;
  @media screen and (min-width: 768px) {
    margin-top: 50px;
    margin-left: 17px;
    margin-right: 0;
    width: 100%;
  }
  @media screen and (min-width: 1280px) {
    margin-top: 51px;
    margin-left: 74px;
  }
}

.about-text-1 {
  font-family: DM Sans;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.62;
  color: #000000;
  margin-bottom: 47px;
  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: 1.76;
    margin-bottom: 34px;
  }
  @media screen and (min-width: 1280px) {
    font-size: 16px;
    margin-bottom: 35px;
  }
}

.about-text-2 {
  font-family: DM Sans;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.62;
  letter-spacing: 0.04em;

  color: #907e82;
  margin-bottom: 15px;
  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: 1.76;
    margin-bottom: 25px;
  }
  @media screen and (min-width: 1280px) {
    line-height: 1.94;
    margin-bottom: 35px;
  }
}

.about-text-3 {
  font-family: DM Sans;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.62;
  letter-spacing: 0.04em;
  margin-bottom: 30px;

  color: #907e82;
  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: 1.76;
    margin-bottom: 62px;
  }
  @media screen and (min-width: 1280px) {
    line-height: 1.94;
    margin-bottom: 45px;
  }
}

.about-readmore-btn {
  display: block;
  text-align: center;
  width: 176px;
  height: 44px;
  margin-left: auto;
  margin-right: auto;

  background: #ffffff;
  border-radius: 22px;
  border: #ffffff;

  font-family: DM Sans;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.28;
  display: flex;
  align-items: center;
  text-align: center;

  color: #d41443;

  align-items: center;
  justify-content: center;
  transition: background 250ms cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;
  &:hover,
  &:focus {
    background: #ecebeb;
  }
  @media screen and (min-width: 768px) {
    font-size: 16px;
    line-height: 1.31;
    margin-bottom: 66px;
    margin-left: 0;
  }
}

.about-readmore-svg {
  margin-left: 14px;
}
